@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);


*{
    font-family: "Roboto" , sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
    text-decoration: none;
    text-transform: capitalize;
    transition: 0.2s linear;
    
}

html {
    font-size: 72.5%;
    overflow-x: hidden;
    scroll-padding-top: 9rem;
    scroll-behavior: smooth;
  }
  
  html::-webkit-scrollbar {
    width: 0.8rem;
  }
  
  html::-webkit-scrollbar-track {
    background: transparent;
  }
  
  html::-webkit-scrollbar-thumb {
    background: #fff;
    border-radius: 5rem;
  }
  
 
 body {
    background: black;
    overflow-x: hidden;
 }


.header {
    background: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 9px 7%;
    border-bottom: 0.2rem solid yellow;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index:1000;
}

.header .logo img{
  max-width: 75px;
  
  
}

.header .navbar a {
    margin: 0 1rem;
    font-size: 1.6rem;
    color: #fff;
}

.header .navbar a:hover{
    color: #ffc107;
    border-bottom: 0.1rem solid #ffc107;
    padding-bottom: 0.5rem;
}
.header .icons div{
    color :#fff;
    cursor: pointer;
    font-size: 2.5rem;
    margin-left: 2rem;

}
  
#menu-btn{
     display: none; 
    
} 

.header .search-form{
    position: absolute;
    top: 115%;
    right: 7%;
    background: #fff;
    width: 50rem;
    height: 5rem;
    display: flex;
    align-items: center;
    transform: scaleY(0);
    transform-origin: top;

}
.header .search-form.active{
    transform: scaleY(1);
}
.header .search-form input{
    height: 100%;
    width: 100%;
    font-size: 1.6rem;
    color: #13131a;
    padding: 1rem;
    text-transform: none;
}

.header .search-form label{
    cursor: pointer;
    font-size: 2.2rem;
    margin-right: 1.5rem;
    color: #13131a;
}
.header .search-form label:hover {
    color: #ffc107;
}
.header .cart-item-container {
    position: absolute;
    top: 100%;
    right: -100%;
    height: calc(100vh - 9.5rem);
    width: 35rem;
    background: #fff;
    padding: 0  1.5rem;
}

.header .cart-item-container.active{
    right: 0;
}

.header .cart-item-container .cart-item{
    position: relative;
    margin: 2rem 0;
    display: flex;
    align-items: center;
    gap: 1.5rem;
}
.header .cart-item-container .cart-item .fa-times{
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    cursor: pointer;
    color: #13131a;
     
}
.header .cart-item-container .cart-item .fa-times:hover {
    color: #ffc107;
}

.header .cart-item-container .cart-item img {
    height: 4.8rem;
}

.header .cart-item-container .cart-item .content h3{
    font-size: 1.25rem;
    color: #13131a;
    padding-bottom: 0.6rem;
}
.header .cart-item-container .cart-item .content .price{
    font-size: 1.2rem;
    color: #ffc107;
}
.header .cart-item-container .btn{
    width: 100%;
    text-align: center;
    
    
}
.header .cart-item-container .btn .a{
    color: #ffc107;
}
 




 .header .navbar{
    position: absolute;
    top: 40%;
    left: 20%;
    right: -100%;
    background: #000;
    width: 30rem;
   
} 

.header .navbar .active{
    right: 0;
}
